<template>
  <div>
    <ElButton title="刷新" @click="handleClickRefresh">
      <ElIcon>
        <Refresh />
      </ElIcon>
    </ElButton>
    <ElButton type="primary" title="添加" @click="handleClickInsert">
      <ElIcon>
        <Plus />
      </ElIcon>
    </ElButton>
  </div>
</template>

<script>
import { Plus, Refresh } from '@element-plus/icons-vue';
import { ElButton, ElIcon } from 'element-plus';

export default {
  name: "repository-projects-toolbar",
  components: { ElButton, ElIcon, Refresh, Plus },

  methods: {

    handleClickInsert() {
      this.fireAction('insert')
    },

    handleClickRefresh() {
      this.fireAction('refresh')
    },

    fireAction(name) {
      this.$emit('action', { name })
    },
  }
};
</script>
