<template>
  <div>
    <!-- <h1>todo: repository-projects</h1> -->
    <MyToolbar class="toolbar" @action="handleAction"></MyToolbar>
    <MyTable :data="projects"></MyTable>
  </div>
</template>

<script>
import MyTable from './table.vue'
import MyToolbar from './toolbar.vue'
import RouteJS from '@/js/route.js'
import GetterJS from '@/js/getter.js'


const route_js = RouteJS.NewRouteJS()


export default {
  name: "repository-projects-index",
  components: { MyTable, MyToolbar },

  computed: {
    theRepositoryID() {
      return route_js.GetRepositoryID(this)
    }
  },

  data() {
    return {
      projects: []
    }
  },

  methods: {

    fetch() {
      let repo_id = this.theRepositoryID;
      let ids = [repo_id]
      this.$store.dispatch('local_repository/fetchByIds', { ids }).then((res) => {
        let vo = res.data;
        this.handleFetchRepositoryVO(vo)
      })
    },

    insert() { },

    handleFetchRepositoryVO(vo) {
      let x = GetterJS.Get(vo, "local_repositories/0/projects", [])
      this.projects = x;
    },

    handleAction(e) {
      let name = e.name;
      if (name == "refresh") {
        this.fetch();
      } else if (name == "insert") {
        this.insert();
      } else {
        console.warn("no handler for action:" + name)
      }
    },

  },

  mounted() {
    this.fetch()
  }
};
</script>

<style>
.toolbar {
  margin-top: 30px;
  margin-bottom: 30px;
}
</style>
